@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,400;0,500;0,700;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  font-family: "Roboto Mono", monospace;
  background-color: #fffcee;
}

.App {
  text-align: center;
}

.Info {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
}

.Foto {
  display: inline;
  margin: -10px auto;
  height: 135%;
  width: auto;
}

.Foto-div {
  width: 200px;
  height: 200px;
  overflow: hidden;
  border-radius: 50%;
}

.Foto-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.icons-row a {
  padding: 4px;
}

.Projetos-page {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.Projeto {
  border-color: #29c25c;
  border-width: 2px;
  border-style: solid;
  margin: 40px;
  max-width: 20%;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Projeto a {
  margin-top: 30%;
  display: contents;
}

.Projeto a img {
  max-height: 100%;
}

.Projeto h3 {
  background-color: #29c25c;
  color: #ffffff;
  margin: 0;
}

.App-header {
  background-color: #29c25c;
  min-height: 7vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #fffcee;
}

.App-header a {
  color: #fffcee;
  text-decoration: none;
  margin: 0 10px;
  padding: 10px;
}
.App-header a:hover {
  background-color: #fffcee;
  color: #29c25c;
}

.icon {
  color: #29c25c;
  size: 20px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

